body {
    background: #000;
  }
  
  .sliding-button {
      width: 120px;
      height: 44px;
    text-decoration: none;
    color: white;
    display: inline-block;
    position: relative;
    /* padding: 15px 30px; */
    border: 1px solid;
    border-image: linear-gradient(180deg, #ff3000, #ed0200, #ff096c, #d50082);
    border-image-slice: 1;
    margin: 10px 20px;
    font-family: "Montserrat", sans-serif;
    text-transform: uppercase;
    overflow: hidden;
    letter-spacing: 2px;
    transition: 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
    background: #000;
  }
  /* стиль для псевдоэлемента над кнопкой, здесь он создаётся */
  .sliding-button:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 0;
    width: 100%;
    z-index: 1;
    /* color: white; */
    background: linear-gradient(180deg, #ff3000, #ed0200, #ff096c, #d50082);
    transition: 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  }
  .sliding-button:hover {
    background: rgba(0, 0, 0, 0);
  }
  .sliding-button:hover:before {
    bottom: 0%;
    top: auto;
    height: 100%;
  }

  .justOMG {
      position: relative;
      top: auto;
      bottom: auto;
      left: auto;
      right: auto;
      display: block;
      z-index: 2;
  }